  /* Login Container */
  .login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  
  /* Login Form */
  .login-form {
    background-color: #ffffff;
    padding: 40px 30px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    text-align: center;
  }
  
  .login-form h1 {
    margin-bottom: 30px;
    font-size: 2.2em;
    color: #333333;
  }
  
  /* Form Group */
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
    color: #555555;
  }
  
  .form-group input {
    width: 100%;
    padding: 12px 15px;
    margin-bottom: 10px;
    border: 1px solid #dddddd;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
    transition: all 0.3s ease;
  }
  
  .form-group input:focus {
    border-color: #007BFF;
    box-shadow: 0 0 8px rgba(0, 123, 255, 0.2);
  }
  
  /* Button */
  .login-button {
    background-color: #007BFF;
    border: none;
    color: #ffffff;
    padding: 12px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  .login-button:hover {
    background-color: #0056b3;
  }
  
  /* Alternative Login */
  .login-alternative {
    font-size: 14px;
    color: #777777;
    margin-top: 15px;
  }
  
  /* Divider */
  .or-divider {
    display: flex;
    align-items: center;
    margin: 30px 0;
  }
  
  .or-divider hr {
    flex: 1;
    border: none;
    border-top: 1px solid #cccccc;
    margin: 0 10px;
  }
  
  .or-divider span {
    font-size: 14px;
    color: #777777;
  }
  
  /* Social Buttons */
  .social-button {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    transition: background-color 0.3s, border-color 0.3s;
  }
  
  .social-button.facebook {
    background-color: #3b5998;
  }
  
  .social-button.google {
    background-color: #dd4b39;
  }
  
  .social-button.facebook:hover {
    background-color: #2d4373;
  }
  
  .social-button.google:hover {
    background-color: #c23321;
  }
  
  /* Signup Link */
  .signup-link {
    display: block;
    margin-top: 20px;
    font-size: 14px;
    color: #007BFF;
    text-decoration: none;
  }
  
  .signup-link:hover {
    text-decoration: underline;
  }
  
  /* Responsive Styles */
  @media (max-width: 450px) {
    .login-form {
      padding: 30px 20px;
    }
  
    .login-form h1 {
      font-size: 1.8em;
    }
  }
  