.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 10px 20px;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.logo .brand {
  font-size: 1.5em;
  font-weight: bold;
}

.nav {
  display: flex;
  align-items: center;
  gap: 10px; /* Adjusted spacing between nav items */
}

.nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 5px; /* Adjusted margin for better spacing */
  font-size: 1em;
}

.nav a {
  display: inline-block;
  padding: 5px 10px;
}

.header .btn-primary {
  background-color: #dc3545; /* Red for Sign Up */
  border: none;
  color: white;
  padding: 8px 12px; /* Adjusted padding */
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px; /* Adjusted font size */
  margin: 5px 10px 5px 0; /* Adjusted margin to reduce space and add right margin */
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.header .btn-primary:hover {
  background-color: #c82333; /* Darker red */
}

.header .btn-secondary {
  background-color: #007bff; /* Blue for Login */
  border: none;
  color: white;
  padding: 8px 12px; /* Adjusted padding */
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px; /* Adjusted font size */
  margin-right: 5px; /* Adjusted margin to reduce space */
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.header .btn-secondary:hover {
  background-color: #0056b3; /* Darker blue */
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #333;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  min-width: 200px;
  top: 100%;
  left: 0;
  font-size: 11px;
}

.dropdown-content a {
  color: #333;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  white-space: nowrap;
}

.dropdown-content a:hover {
  background-color: black;
}

.dropdown:hover .dropdown-content {
  display: block;
}