.services {
    padding: 50px 0;
  }
  
  .section-title {
    text-align: center;
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  
  .section-subtitle {
    text-align: center;
    font-size: 18px;
    margin-bottom: 40px;
  }
  
  .line {
    display: block;
    width: 50px;
    height: 3px;
    background-color: #ff4d4d;
    margin: 10px auto 0;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; /* Adjusted spacing between columns */
    gap: 20px;
  }
  
  .col-md-4 {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    margin-bottom: 20px;
  }
  
  .service-card {
    background-color: #333;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 85%;
    border: 2px solid #ff4d4d;
    margin-bottom: 20px; /* Add margin-bottom for space between each card */
  }
  
  
  .service-card h3 {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
    color: white; /* Added color for better contrast */
  }
  
  .service-card p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 20px;
    color: white; /* Added color for better contrast */
  }
  /* Services.css */
.icon {
  font-size: 40px; /* Adjust the size as needed */
  color: white;
}
