.plagiarism-checker-container {
    max-width: 600px;
    margin: 100px auto 0; /* Added margin to the top */
    padding: 20px;
    border: 2px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .plagiarism-checker-container h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .plagiarism-checker-container .form-group {
    margin-bottom: 20px;
  }
  
  .plagiarism-checker-container label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .plagiarism-checker-container textarea {
    width: 100%;
    height: 120px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
  }
  
  .plagiarism-checker-container button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .plagiarism-checker-container button:hover {
    background-color: #0056b3;
  }
  
  .plagiarism-checker-container .result {
    margin-top: 20px;
    padding: 10px;
    background-color: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
    border-radius: 5px;
  }
  