/* General Styles */
.blog-container {
  width: 100%;
  max-width: 900px;
  margin: 100px auto 0; /* Added margin to the top */
  padding: 20px;
  font-family: Arial, sans-serif;
}

.blog-title {
  text-align: center;
  font-size: 2.5em;
  color: #333;
  margin-bottom: 30px;
}

.blog-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.blog-post {
  background-color: #f9f9f9; /* Lighter background color */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.post-title {
  font-size: 1.8em;
  margin-bottom: 10px;
  color: #007BFF;
}

.post-content {
  font-size: 1.1em;
  color: #555;
  margin-bottom: 20px;
}

.post-meta {
  font-size: 0.9em;
  color: #777;
}

.post-author {
  font-weight: bold;
}

@media (max-width: 768px) {
  .blog-container {
    padding: 15px;
  }

  .blog-title {
    font-size: 2em;
  }

  .post-title {
    font-size: 1.5em;
  }

  .post-content {
    font-size: 1em;
  }
}
