.about-us-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
  margin-top: 100px; /* Added margin to the top */
}

.about-us-content {
  width: 50%;
}

.about-us-heading {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.about-us-text {
  font-size: 1.2rem;
  line-height: 1.6;
}

.about-us-images {
  width: 40%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.about-us-image-container {
  position: relative;
  width: 100%;
  height: 250px;
  border-radius: 10px;
  overflow: hidden;
}

.about-us-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
